@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

*{
    box-sizing: border-box;
    font-family: "Inter", serif;
    background-color: #bdc3c7;
   
}

.app-container{
    width: 100%;
    height: 100vh;  
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   
    
}
.app-container h1{
    font-weight: 600;
    font-size: 20px;
    color:#3498db;
    border-bottom:  1px solid #f1efef;
    padding: 10px 0px;
}

.imgage{
    padding: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.input-label{
    display: block;
    margin-bottom: 8px;
    color: #3498db;
    font-size: 15px;
    font-weight: 500;
}
input[type="text"]{
    padding: 10px;
    margin-bottom: 20px;
    width: 100%;
    font-size: 16px;
    border: 2px solid #3498db;
    border-radius: 5px;
}
button{
    padding: 15px 20px;
    font-size: 18px;
    color: aqua;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    
    transition: background-color 0.3s;
}
.Generate-button{
    background-color: #3498db;
}
.Generate-button:disabled{
    background-color: #bdc3c7;
    cursor: not-allowed;
}
.Download-button{
    background-color: #27ae60;
    margin-left: 20px;

}
.Download-button:hover{
   background-color: #6de4a2;
}